<template>
    <div class="main-center main-center-3">
        <div class="wrap-nav">
            <!-- <a href="javascript:void(0)">课程学习</a> &gt; -->
            <a href="javascript:void(0)" @click="goView('checkEvaluation')">测评中心</a> &gt;
            <a href="javascript:void(0)">测评详情</a>
        </div>
        <div class="lesson-detail-box">
            <div class="flex-box">
                <div class="flex-left-box">
                    <p class="lesson-box">
                        <span class="lesson-name">{{detailInfo.lesson_name}}</span>
                    </p>
                    <p class="course-name">{{detailInfo.course_name}}</p>
                    <p class="date"><span v-if="detailInfo.is_one_day==='1'">{{detailInfo.lesson_day}}</span>
                        {{detailInfo.lesson_start_time}} - {{detailInfo.lesson_end_time}}
                    </p>
                    <div class="teacher-info-box">
                        <img v-if="detailInfo.teacher_image" :src="detailInfo.teacher_image" alt="">
                        <div v-if="detailInfo.teacher_name" class="teacher-name">{{detailInfo.teacher_name}}</div>
                    </div>
                </div>
                <!-- <div class="flex-right-box">
                    <div class="course-play-box report-btn" @click="goUnitReport">
                        <img src="@/assets/img/report-btn.png" alt="">
                        <p>测评报告</p>
                    </div>
                </div> -->
            </div>
            <p class="mod-tit" v-if="detailInfo.key_points">
                <span class="left-bor"></span>
                <span class="mod-txt">测评重点</span>
            </p>
            <p class="mubiao-txt"
               v-if="detailInfo.key_points"
               style="word-break: break-all">
                {{detailInfo.key_points}}
            </p>


            <p class="mod-tit">
                <span class="left-bor"></span>
                <span class="mod-txt">测评规则</span>
            </p>
            <p class="mubiao-txt">请同学们阅读以下测评规则和说明，提前做好复习准备：<br>
                1. 测评中可能存在听力题，请提前设置好设备音量；<br>
                2. 测评中可能存在听力和口语题，请使用电脑测评的学生提前准备好带有麦克风功能的耳机；<br>
                3. 语音测试题仅播放两遍，注意听题；<br>
                4. 本次测评将启用摄像头监考，请在测评前确保设备的摄像头可用；<br>
                5. 为了更好地了解同学们的学习情况，请务必独立完成测评；</p>
        </div>
        <div class="lesson-detail-footer">
            <button v-if="detailInfo.button" 
              @click="goUnitTest(detailInfo)" :class="{'btn-disabled':detailInfo.button.enable==='0'}">{{detailInfo.button.button_name}}
            </button>
        </div>
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    export default {
        data: () => ({
            detailInfo:{}
        }),
        methods: {
            ...mapActions('course', ['getLessonExaminationDetailActions']),
            goUnitReport(){
                this.goView('unitTestDetail', {
                    lesson_id: this.detailInfo.lesson_id,
                    lesson_name: this.detailInfo.lesson_name
                })
            },
            getLessonExaminationDetail(){
                let params = {
                    lesson_id:this.$route.query.lesson_id
                }
                this.getLessonExaminationDetailActions(params).then((res)=>{
                    let {error_code, data} = res;
                    if(!error_code){
                        this.detailInfo = data
                    }
                })
            },
            goUnitTest(detail){
                if(detail.button.enable==='0'){
                    this.$message.warning(detail.button.button_message)
                    return
                }
                if(detail.button.button_name==='查看报告'){
                    this.goUnitReport()
                }else{
                    this.goView('beforeTestUnit',{lesson_name:detail.lesson_name,lesson_id:detail.lesson_id})
                }
            }
        },
        mounted() {
            this.getLessonExaminationDetail()
        },
        computed: {},
        components: {

        },
        created() {
        }
    }
</script>

<style lang="postcss" scoped></style>
