<script>
    import star from '@/common/star.vue'
    import {mapActions} from 'vuex'
//    import selectTestLevel from '@/components/checkEvaluation/select-test-level.vue'
    export default {
        data: () => ({
            curTab:'1',//1:单元测评  2:入学测评
            evaluationList: [],//入学测试列表
            unitEvaluationList: [],//单元测试列表
            circleOptions: {
                width: 100,
                height: 100,
                border: 20,
                curBorderColor: 'orange'
            },
            listLoading: false,
            page:{
                unit:{
                    pageNo: 1,
                    pageSize: 4,
                    total: 0,
                },
                study:{
                    pageNo: 1,
                    pageSize: 4,
                    total: 0,
                }
            }
        }),
        methods: {
            ...mapActions('checkEvaluation', ['getTestResultListActions','getLessonExaminationListActions']),
            goUnitTest(item){
                if(item.button.enable==='0'){
                    this.$message.warning('测评还未开始')
                    return
                }
                this.goView('beforeTestUnit',{lesson_name:item.lesson_name,lesson_id:item.lesson_id})
            },
            goUnitTestDetail(item){
                this.goView('checkEvaluationDetail',{lesson_id:item.lesson_id})
            },
            goEvalTest(item){
                if(item.can_test==='0'){
                    this.$message.warning(item.toast_message)
                    return
                }
                this.$router.push({
                    name:'beforeTest'
                })
            },
            tabCurTab(type){
                this.curTab = type
            },
            //跳转到入学测评报告页面
            goDetail(item) {
                this.goView('testDetail', {
                    test_id: item.test_id,
                    test_name: item.test_name
                })
            },
            //跳转到单元测评报告页面
            goUnitReport(item){
                if(item.test_result.has_report==='0'){
                    this.$message.warning('暂时还不能查看报告')
                    return
                }
                this.goView('unitTestDetail', {
                    lesson_id: item.lesson_id,
                    lesson_name: item.lesson_name
                })
            },
            getLessonExaminationList(){
                let params = {
                    page_num: this.page.unit.pageNo,
                    page_size: this.page.unit.pageSize
                }
                this.listLoading = true
                this.getLessonExaminationListActions(params).then((res) => {
                    this.listLoading = false
                    let {error_code, data} = res
                    if (!error_code) {
                        this.unitEvaluationList = data
                        this.unitEvaluationList.forEach((item)=>{
                            if(!item.test_result){
                                this.$set(item,'test_result',{})
                                //item.test_result.user_star_num = Number(item.test_result.user_star_num)
                            }else{
                                item.test_result.user_star_num = Number(item.test_result.user_star_num)
                            }
                        })
                        this.page.unit.total = data.total
                    }
                }, () => {
                    this.listLoading = false
                })
            },
            getTestResultList() {
                let params = {
                    page_num: this.page.study.pageNo,
                    page_size: this.page.study.pageSize
                }
                this.listLoading = true
                this.getTestResultListActions(params).then((res) => {
                    this.listLoading = false
                    let {error_code, data} = res
                    if (!error_code) {
                        this.evaluationList = data.rows
                        this.evaluationList.forEach((item) => {
                            item.accuracy = Number(item.accuracy)
                            item.star_num = Number(item.star_num)
                        })
                        this.page.study.total = data.total
                    }
                }, () => {
                    this.listLoading = false
                })
            }
        },
        mounted() {
            if(this.$route.query.curTab){
                this.curTab = this.$route.query.curTab
            }
            this.getTestResultList()
            this.getLessonExaminationList()
        },
        computed: {},
        components: {
//            circleProgress,
            star
//            selectTestLevel
        },
        created() {
        }
    }
</script>

