<template>
    <div class="main-center main-center-4">
        <!--<div class="before-test-box">
            <div class="position-tit-box">
                英语能力测评
            </div>
            <div class="title">来测测Ta的英语小宇宙有多强大吧~</div>
            <p>请让孩子独立完成</p>
            <p>请把音量开到最大</p>
            <p>语音测试题可播放两遍</p>
            <div class="box">
                <button class="btn btn-orange"
                        @click="showTestDialog = true">开始测评</button>
            </div>
            <div class="test-bottom-bg"></div>
        </div>
        <div class="dialog" v-if="showTestDialog">
            <div class="dialog-mask"></div>
            <div class="dialog-main text-level-content">
                <div class="dialog-header">
                    <span class="close" @click="showTestDialog=false">x</span>
                </div>
                <div class="dialog-content">
                    <select-test-level style="margin-top: 4rem"></select-test-level>
                </div>
            </div>


        </div>-->


        <div class="before-test-box-2">
            <div class="position-tit-box">
                英语能力测评
            </div>
            <div class="title">来测测Ta的英语小宇宙有多强大吧~</div>
            <div class="intro">
                <p>请让孩子独立完成</p>
                <p>请把音量开到最大</p>
                <p>语音测试题可播放两遍</p>
            </div>
            <div class="before-test-step">
                <ul class="step-list">
                    <!--ed:完成的  current当前正在进行的-->
                    <li class="step-item"
                        :class="{'current':item.is_done==='1'&&item.is_pass==='0','ed':item.is_pass==='1'}"
                        :key="index"
                        v-for="(item,index) in stageInfo.test_result_list">
                        <div class="step-item-head">
                            <span class="num"></span>
                            <div class="step-item-line"></div>
                        </div>
                        <div class="step-item-content">
                            {{item.stage_name}}
                            <star v-model="item.user_star_num"
                                  v-if="item.is_done==='1'"
                                  class="star-list-1"></star>
                        </div>
                    </li>
                    <!--<li class="step-item current">
                        <div class="step-item-head">
                            <span class="num"></span>
                            <div class="step-item-line"></div>
                        </div>
                        <div class="step-item-content">
                            part2
                        </div>
                    </li>
                    <li class="step-item">
                        <div class="step-item-head">
                            <span class="num"></span>
                            <div class="step-item-line"></div>
                        </div>
                        <div class="step-item-content">
                            part3
                        </div>
                    </li>-->
                </ul>
            </div>
            <!--{{stageInfo.can_test}} {{count}}-->
            <div class="box" v-if="stageInfo.can_test==='1' && count<=0">
                <button class="btn btn-orange" @click="goTest">开始测评</button>
            </div>
            <div class="daojishi-counter slaceCount" v-if="count>0 && stageInfo.can_test==='1'">
                {{count}}
            </div>

            <div class="test-msg">{{stageInfo.test_warn}}</div>
            <div class="test-result" v-if="stageInfo.is_already_test==='1'">
                <a href="javascript:void(0)" @click="goView('checkEvaluation',{curTab:'2'})">查看学习测评结果></a>
            </div>
            <div class="test-bottom-bg"></div>
        </div>


    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    import star from '@/common/star.vue'
//    import selectTestLevel from '@/components/checkEvaluation/select-test-level.vue'
    export default {
        data: () => ({
            showTestDialog:false,
            stageInfo:{},
            count:5
        }),
        methods:{
            ...mapActions('checkEvaluation',['getTestResultListActions','getTestStageActions']),
            getTestStage(){
                this.getTestStageActions().then((res)=>{
                    let {error_code,data} = res;
                    if(!error_code){
                        this.stageInfo = data
                        if(this.stageInfo.can_test==='1'){
                            this.setTimerCount()
                        }
                        this.stageInfo.test_result_list.forEach((item)=>{
                            item.user_star_num = Number(item.user_star_num)
                        })
                    }
                })
            },
            goTest(){
                this.goView('startTest',{stage_type:this.stageInfo.test_stage_type})
            },
            //倒计时54321
            setTimerCount(){
                let timer = setInterval(()=>{
                    this.count --
                    if(this.count<=0){
                        clearInterval(timer)
                    }
                },1000)
            }
        },
        mounted(){
            this.getTestStage()
        },
        deactivated(){
            this.showTestDialog = false
        },
        destoryed() {
            this.showTestDialog = false
        },
        computed: {},
        components: {
            star
//            selectTestLevel
        },
        created() {
        }
    }
</script>

<style lang="postcss" scoped>

</style>
