                let res = ''
                let min = this.testLong / 60 < 10 ? ('0'+Math.floor(this.testLong / 60)) : +Math.floor(this.testLong / 60)
                let sed = this.testLong % 60 < 10 ? ('0'+this.testLong % 60) : this.testLong % 60
                res = min + ':' + sed
                this.timeStr = res
                this.testLong --
            }*/
        },
        mounted(){
            /*this.testLong = Number(this.$route.query.testLong)
            this.getTimeStr()
            setInterval(()=>{
                this.getTimeStr()
            },1000)*/

            let timer = setInterval(()=>{
